.rtl *:not(.token) {
  font-family: Vazir;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.navbar {
  height: 22px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background-color: white;
  box-shadow: 0 2px 2px -3px rgb(115, 115, 116);
  padding: 20px 10px;
}

.icons {
  display: flex;
  float: right;
  position: absolute;
  right: 20px;
  top: 22px;
}

.icon {
  margin-left: 20px;
}

.icon .npm svg {
  width: 40px;
  height: 15px;
}

.icon .github svg {
  width: 20px;
  height: 20px;
}

.icon.language svg {
  width: 30px;
  margin-top: 2px;
}

.language {
  cursor: pointer;
}

.home a {
  text-decoration: none;
  color: inherit;
}

.home a svg {
  margin-top: -2px;
}

.sidebar {
  width: 270px;
  position: fixed;
  left: 0;
  bottom: 0;
  top: 62px;
  list-style: none;
  margin: 0;
  padding: 10px 0;
  padding-left: 10px;
  background-color: white;
  box-shadow: 2px 2px 2px -2px #ccc;
  overflow: auto;
  background-color: white;
}

.sidebar li {
  padding: 5px 20px;
  font-size: 14px;
}

.sidebar li a {
  text-decoration: none;
  color: inherit;
}

.sidebar-sub-menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.main {
  position: fixed;
  left: 280px;
  top: 62px;
  right: 0;
  bottom: 0;
  padding: 10px;
  overflow: auto;
}

.navbar .navbar-title {
  font-weight: bold;
  margin-bottom: 5px;
  position: absolute;
  left: 10px;
}

.navbar .navbar-title .name::after {
  content: ":";
}

.example {
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
}

table {
  border: 1px solid #ccc;
}

table th,
table td {
  text-align: center;
  border: 1px solid #ccc;
  padding: 2px 10px;
}

.open-sidebar {
  cursor: pointer;
  visibility: hidden;
}

.navbar .navbar-title {
  margin-left: 5px;
  display: inline-block;
  font-weight: unset;
}

.rtl .sidebar {
  right: 0;
  left: auto;
  padding: 10px 0;
  padding-left: 0;
  padding-right: 10px;
  box-shadow: 2px 2px 2px 2px #ccc;
  direction: rtl;
}

.rtl .main {
  left: 0;
  right: 280px;
  direction: rtl;
}

.rtl .main pre,
.rtl .main pre code,
.rtl .main pre code span {
  direction: ltr;
}

@media (max-width: 710px) {
  .open-sidebar {
    visibility: visible;
    position: absolute;
    left: 15px;
    top: 17px;
  }

  .navbar .navbar-title {
    left: 50px;
  }

  .navbar .navbar-title .name {
    display: none;
  }

  .sidebar {
    left: -305px;
    transition: 0.4s;
    box-shadow: 3px 2px 6px -2px #ccc;
    z-index: 1;
  }

  .sidebar.active {
    left: 0;
    transition: 0.4s;
  }

  .rtl .sidebar {
    right: -305px;
    left: auto;
    box-shadow: 3px 2px 6px 2px #ccc;
  }

  .rtl .sidebar.active {
    right: 0;
    left: auto;
  }

  .main {
    left: 0px;
  }

  .rtl .main {
    left: 0;
    right: 0;
  }
}

@media (max-width: 400px) {
  .navbar .navbar-title {
    display: none;
  }
}

*::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}

*::-webkit-scrollbar-thumb {
  background: #0074d9;
  border-radius: 6px;
}

*::-webkit-scrollbar-thumb:hover {
  background: #0266be;
}

pre {
  background-color: #464545 !important;
}

.calendar-border {
  border: 2px black solid!impoetant;
}
